.name-form {
  width:670px;
  margin: 0 auto;
  margin-top:126px;
  text-align:center;
}
input {
  display:inline-block;
  border:0;
  padding:0;
  margin:0;
  background:none;
}
.name-form label {
  display:block;
  width:275px;
  height:58px;
  margin:0 auto 25px;
  background:url('./images/txt-name.png') 0 0 no-repeat;
}
.name-form .inp-text {
  width:669px;
  font-size:57px;
  line-height:63px;
  padding:32px 0 34px;
  font-family:"Noto Sans KR", sans-serif;
  text-align:center;
  color:#2f5340;
  border-radius:12px;
  -webkit-box-shadow:18px 18px 0 #2f5340;
          box-shadow:18px 18px 0 #2f5340;
  background:white;
}
.name-form .btn-next {
  margin-top:110px;
  width:351px;
  height:119px;
  padding:0;
  border:0;
  background:url('./images/btn-next.png') 0 0 no-repeat;
}


