h1,h2,h3,h4,h5,h6,p,ul,ol,li,dl,dt,dd {
  margin:0;
  padding:0;
}
* {
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}
.blind {
  position:absolute;
  z-index:-1;
  visibility: hidden; 
}
body {
  margin: 0;
  background:#f5b93f;
}

header {
  padding-top:56px;
  height:265px;
  background:url('./images/bg-header.png') 0 0 repeat-x;
}
header h1 {
  width:1524px;
  height:64px;
  background:url('./images/img-title.png') 0 0 no-repeat;
  margin:0 auto;
}
.tree-1 {
  position:fixed;
  left:136px;
  bottom:0;
  width:480px;
  height:528px;
  background:url('./images/img_tree_1.png') 0 0 no-repeat;
}
.tree-2 {
  position:fixed;
  right:134px;
  bottom:0;
  width:416px;
  height:523px;
  background:url('./images/img_tree_2.png') 0 0 no-repeat;
}
.result {
  width:1130px;
  margin:0 auto;
}
.result-text {
  height:62px;
}
.result-text strong {
  display:inline-block;
  width:478px;
  line-height:auto;
  font-size:36px;
  line-height:62px;
  font-family:"Noto Sans KR", sans-serif;
  text-align:center;
  font-weight:bold;
}
.result-text.state-start {
  background:url('./images/txt-start.png') right center no-repeat;
}
.result-text.state-timing {
  background:url('./images/txt-timing.png') right center no-repeat;
}
.result-text.state-end {
  background:url('./images/txt-end.png') right center no-repeat;
}
.result-textarea {
  font-size:60px;
  line-height:1.8;
  font-family:"Noto Sans KR", sans-serif;
  background:white;
  padding:57px;
  width:1130px;
  height:900px;
  margin-top:26px;
  -webkit-box-shadow:20px 20px 0 #2f5340;
          box-shadow:20px 20px 0 #2f5340;
}


