.loading {
  position:relative;
  width:910px;
  margin:393px auto 0;
}
.loading-text {
  width:781px;
  height:72px;
  background:url('./images/txt-loading.png') 0 0 no-repeat;
}
.loading-dots-group {
  position:absolute;
  top:33px;
  right:0;
  overflow:hidden;
}
.loading-dots {
  float:left;
  margin-left:29px;
  width:8px;
  height:8px;
  background:#2f5340;
  border-radius:50%;
  opacity: 0;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name:dots-animation;
          animation-name:dots-animation;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-fill-mode:forwards;
          animation-fill-mode:forwards;
}
.loading-dots-1 {
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}
.loading-dots-2 {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}
.loading-dots-3 {
  -webkit-animation-delay: 2.5s;
          animation-delay: 2.5s;
}
.loading-dots + .loading-dots {
  margin-left:37px;
}

@-webkit-keyframes dots-animation {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@keyframes dots-animation {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}


