h1,h2,h3,h4,h5,h6,p,ul,ol,li,dl,dt,dd {
  margin:0;
  padding:0;
}
* {
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}
.blind {
  position:absolute;
  z-index:-1;
  visibility: hidden; 
}
body {
  margin: 0;
  background:#f5b93f;
}

header {
  padding-top:56px;
  height:265px;
  background:url('./images/bg-header.png') 0 0 repeat-x;
}
header h1 {
  width:1524px;
  height:64px;
  background:url('./images/img-title.png') 0 0 no-repeat;
  margin:0 auto;
}
.tree-1 {
  position:fixed;
  left:136px;
  bottom:0;
  width:480px;
  height:528px;
  background:url('./images/img_tree_1.png') 0 0 no-repeat;
}
.tree-2 {
  position:fixed;
  right:134px;
  bottom:0;
  width:416px;
  height:523px;
  background:url('./images/img_tree_2.png') 0 0 no-repeat;
}
.desc {
  background:url('./images/txt-entry.png') 0 0 no-repeat;
  width:692px;
  height:122px;
  margin: 122px auto 0;
}


